/* Show pointer to indicate column header is clickable */
thead th span {
  cursor: pointer;
}

/* Following classes allows for table to be scrollable */
tbody {
  display: block;
  overflow-y: auto;
  overflow-x: hidden;
  max-height: 65vh;
}

thead,
tbody tr {
  display: table;
  width: 100%;
  table-layout: fixed;
}

.email {
  overflow-x: auto;
}
